import React, { useEffect, useState } from "react";
import dummySpeaker from "../images/speakers/halftoneAvatar.png";
import twitterLogo from "../images/x.png";
// For some reason, the github logo is not loading so we use the svg instead
// import githubLogo from "../images/github.png";
import websiteLogo from "../images/website.png";
import matrixLogo from "../images/matrix.png";

function getAlphab(index) {
  if (index < 0) {
    return null; // Index out of range for alphabetical characters
  }
  if (index > 25) {
    return getAlphab(index / 26 - 1) + getAlphab(index % 26); // Index out of range for a single alphabetical character
  }

  var charCode = "a".charCodeAt(0) + index;
  return String.fromCharCode(charCode);
}

const Speaker = ({ speaker, index }) => {
  const { twitter, github, website, matrix, name, image, organization } = speaker;
  const src = image?.default || speaker?.image || dummySpeaker;
  const [isDarkMode, setIsDarkMode] = useState(false);

  // Check if dark mode is active
  useEffect(() => {
    const checkDarkMode = () => {
      // Check specifically for the latex-dark class on the body
      const isDark = document.body.classList.contains("latex-dark");
      console.log("Dark mode detected:", isDark);
      setIsDarkMode(isDark);
    };

    // Initial check
    checkDarkMode();

    // Set up a MutationObserver to detect changes in the body class
    const observer = new MutationObserver(checkDarkMode);
    observer.observe(document.body, {
      attributes: true,
      attributeFilter: ["class"],
    });

    return () => {
      observer.disconnect();
    };
  }, []);

  return (
    <div className="flex flex-col items-center justify-center mt-6">
      <div className="flex justify-center items-center h-40 w-40 overflow-clip">
        <img
          src={src}
          alt={name}
          className={`grayscale ${
            src !== dummySpeaker ? "brightness-125 opacity-70 contrast-100" : ""
          } object-contain w-48`}
        />
      </div>
      <div className="text-center mt-1">
        <div>
          {"(" + getAlphab(index) + ") "}
          <b>{name}</b>
        </div>
        {organization && <div className="">{organization.name}</div>}
        <div className="flex flex-row justify-center gap-4 mt-2">
          {twitter && (
            <>
              <a href={twitter} rel="noopener noreferrer" target="_blank">
                <img
                  src={twitterLogo}
                  alt="Twitter"
                  className={`w-4 opacity-50 hover:opacity-100 ${isDarkMode ? "invert" : ""}`}
                />
              </a>
            </>
          )}
          {github && (
            <>
              <a href={github} rel="noopener noreferrer" target="_blank" className="github-link">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  viewBox="0 0 24 24"
                  className="opacity-50 hover:opacity-100"
                >
                  <path
                    fill={isDarkMode ? "white" : "black"}
                    d="M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z"
                  />
                </svg>
              </a>
            </>
          )}
          {website && (
            <>
              <a href={website} rel="noopener noreferrer" target="_blank">
                <img
                  src={websiteLogo}
                  alt="Website"
                  className={`w-5 opacity-50 hover:opacity-100 ${isDarkMode ? "invert" : ""}`}
                />
              </a>
            </>
          )}
          {matrix && (
            <>
              <a href={"https://matrix.to/#/" + matrix} rel="noopener noreferrer" target="_blank">
                <img
                  src={matrixLogo}
                  alt="Matrix"
                  className={`w-5 opacity-50 hover:opacity-100 ${isDarkMode ? "invert" : ""}`}
                />
              </a>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default Speaker;
